import React from "react";
// import PropTypes from "prop-types";
import { Carousel } from "../../components";
import Button from "@mui/material/Button";
import Grid from "@mui/material/Grid";
import Container from "@mui/material/Container";
import { makeStyles } from "@mui/styles";
// import { getOnboard } from "../../redux/actions";
import { connect } from "react-redux";
import { useNavigate } from "react-router-dom";
const useStyles = makeStyles({
  rounded: {
    borderRadius: "10px !important",
    width: "100%",
  },
  root: {
    height: "100vh",
  },
});

const OnBoardingPage = (props) => {
  const { error } = props;

  const navigate = useNavigate();

  if (error) {
    navigate("/exception-handling");
  }
  const classes = useStyles();
  const toHome = () => {
    navigate("/");
  };
  const toRegister = () => {
    navigate("/register");
  };
  React.useEffect(() => {
    // actGetOnboard();
  }, []);
  let carousel = [];
  [0].map((item) => {
    carousel.push({
      image: "./assets/doctors.png",
      text: "How can we help you?",
      title: "",
    });
  });

  return (
    <Container>
      <Grid
        container
        spacing={4}
        className={classes.root}
        justifyContent="center"
        alignItems="center"
      >
        <Grid item xs={12}>
          <div className={classes.rounded}>
            <Carousel type="big" carousel={carousel} />
          </div>
        </Grid>
        <Grid container item xs={12} spacing={2}>
          <Grid item xs={12}>
            <Button
              variant="outlined"
              fullWidth
              onClick={toRegister}
              className={classes.buttonFoot}
            >
              DAFTAR PASIEN BARU
            </Button>
          </Grid>
          <Grid item xs={12}>
            <Button
              disabled
              variant="contained"
              fullWidth
              onClick={toHome}
              className={classes.buttonFoot}
            >
              START
            </Button>
          </Grid>
        </Grid>
      </Grid>
    </Container>
  );
};

const mapStateToProps = (state) => {
  return {
    error: state.app.error,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    // actGetOnboard: (body, action) => () => {},
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(OnBoardingPage);
